import React, { Component } from 'react';

import AppHeader from '../app-header';
import SearchPanel from '../search-panel';
import TodoList from '../todo-list';
import ItemStatusFilter from '../item-status-filter';
import ItemAddForm from '../item-add-form';

import './app.css';

export default class App extends Component {

  maxId = 100;

  createItem = (label) => {
      return{
        label,
        important: false,
        done: false,
        id: this.maxId++
      }
  };

  state = {
    todoData : [
      this.createItem('Drink Coffee'),
      this.createItem('Make Awesome App'),
      this.createItem('Have a lunch'),
    ],
    term: '',
    filter: 'all'
  };
  
  deleteItem = (id) => {
      this.setState(({todoData}) => {
        const idx = this.state.todoData.findIndex((el) => el.id === id);

        return {
          todoData: [ ...todoData.slice(0, idx), ...todoData.slice(idx + 1) ]
        }
      });
    };

  addItem = (text) => {
    const newItem = this.createItem(text);

    this.setState(({todoData}) => {
      const newArr = [
        ...todoData,
        newItem
      ]
      return{
        todoData: newArr
      }
    });
  };

  toggleDone = (id) => {
    const idx = this.state.todoData.findIndex((el) => el.id === id);
    const newItem = {...this.state.todoData[idx], done: !this.state.todoData[idx].done };

    const newArr = [
      ...this.state.todoData.slice(0, idx),
      newItem,
      ...this.state.todoData.slice(idx + 1)
    ];

    this.setState(({todoData}) => {
      return {
        todoData: newArr
      }
    })
  };

  toggleImportant = (id) => {
    
    const idx = this.state.todoData.findIndex((el) => el.id === id);
    
    const newItem = { ...this.state.todoData[idx], important: !this.state.todoData[idx].important };

    this.setState(({todoData}) => {
      return{
      todoData: [
        ...this.state.todoData.slice(0, idx),
        newItem,
        ...this.state.todoData.slice(idx + 1)
      ]
    } 
  })
}

  onSearchChange = (term) => {
    this.setState({ term })
    }

  onFilterChange = (filter) => {
    this.setState({ filter })
  }

  filter(arr, filter) {
    switch(filter) {
      case 'all' :
        return arr;
      case 'active':
        return arr.filter((item) => !item.done);
      case 'done':
        return arr.filter((item) => item.done);
      default: 
        return arr
    }
  }

  search (arr, term) {
    if(term.length === 0){
      return arr
    };

    return arr.filter((item) => {
      return item.label.toLowerCase().indexOf(term.toLowerCase()) > -1
    })
  }

  render(){

    const { todoData, term, filter } = this.state;

    let visibleItems = this.filter(this.search(todoData, term), filter);

    let toDoneCount = todoData.filter((el) => el.done).length;
    let toDoCount = todoData.length - toDoneCount;

    return (
      <div className="todo-app">
        <AppHeader toDo={toDoCount} done={toDoneCount} />
        <div className="top-panel d-flex">
          <SearchPanel 
            onSearchChange = {this.onSearchChange}/>
          <ItemStatusFilter 
            filter = { filter }
            onFilterChange = {this.onFilterChange }/>
        </div>

        <TodoList 
          todos={ visibleItems }
          onDeleted={(id) => this.deleteItem(id) } 
          onToggleDone = {(id) => this.toggleDone(id) }
          onToggleImportant = {(id) => this.toggleImportant(id)}/>

        <ItemAddForm 
          addedItem={ this.addItem }/>
      </div>
    );
  }
};